.emi-avatar {
  display: inline-flex;
  align-items: center;
  justify-content: center;

    background: linear-gradient(135deg, #051aa3, #010307);
  color: white;
  font-family: "Inter var", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  font-weight: 600;
  padding: 20px 30px;
  border-radius: 50px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3);
  font-size: 18px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
}

/* Hover effect */
.emi-avatar:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 25px rgba(0, 0, 0, 0.4);
}

/* Optional: make text slightly larger on hover */
.emi-avatar:hover .emi-text {
  font-size: 20px;
}
