.emi-face-mood-page {
  display: grid;
  place-items: center;
  gap: 24px;
  padding: 40px 20px;
  min-height: 100vh;
  background-color: #0d0d0d; /* pure black background */
  color: #ffffff;
  font-family: "Inter var", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  text-align: center;
}

/* Page title styling */
.page-title {
  font-size: 32px;
  font-weight: 800;
color: white;
 
  
}

/* EmiAvatar customization for the page */
.emi-face-mood-page .emi-avatar {
  width: fit-content;
  padding: 20px 40px;
  font-size: 20px;
}

/* FaceMoodDetector container (optional) */
.emi-face-mood-page .face-mood-detector {
  background: #1a1a1a;
  padding: 20px;
  border-radius: 20px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.5);
  width: 400px;
  max-width: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Hover effect for interactive sections */
.emi-face-mood-page .face-mood-detector:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 35px rgba(0, 0, 0, 0.6);
  transition: all 0.3s ease;
}
